
@use '../components/mixin' as *;
@use '../components/variables' as *;
@use '../components/icons' as *;


#wrap {

    // 공통
    .local-body {
        .section {
            padding-top: 12rem;

            @include mobile {
                padding-top: 8rem;
            }
            + .section {
                padding-top: 18rem;

                @include mobile {
                    padding-top: 12rem;
                }
            }
        }
    }


    .solution-intro{
        .section{
            //bg
            &.esg-operation{
                position: relative;
                padding: 10rem 0 8.9rem;

                @include mobile {
                    padding: 6rem 0 7.9rem;
                   
                    .section-figure {
                        @include mobile {
                            @include scroll-x;
                        }
                        img {
                            width: 91rem;
                        }
                    }
                }

                &::after{
                    position: absolute;
                    top: 0;
                    left: 50%;
                    width: 100vw;
                    height: 100%;
                    content: '';
                    background: custom-color(etc, 070);
                    transform: translateX(-50%);
                    z-index: -1;

                }

                .section-body {
                    text-align: center;
                }
            }

            .section-body {
                text-align: center;
                &.solution-character {
                    padding: 6.6rem 8rem;
                    border-radius: 1.6rem;
                    background: custom-color(etc, 070);
                    @include mobile {
                        padding: 4rem 1.6rem;
                    }

                    >.data-list {
                        .data-item {
                            .bullet-list {
                                display: flex;
                                flex-wrap: wrap;
                                column-gap: 7rem;
                                width: 111rem;
                                .bullet-item {
                                    width: 47rem;
                                }

                                @include mobile {
                                    flex-direction: column;
                                }
                            }
                        }
                    }
                }

                .click-esg-manager {
                    overflow-y: auto;
                    .circle {
                        position: relative;
                        min-width: 91rem;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        margin: 0 auto;
                        height: 80rem;

                        @include mobile {
                            margin-left: 10rem;
                        }

                        .circles {
                        
                            .item {
                                position: absolute;
                                top: -0.9rem;
                                left: calc(50% + 11rem);
                                transform: translateX(-50%);
                                height: 17.8rem;
                                .case {
                                    width: 37rem;
                                    height: 100%;
                                }

                                .icon {
                                    position: absolute;
                                    top: 4rem;
                                    left: 4.5rem;
                                    display: block;
                                    width: 7.6rem;
                                    height: 7.6rem;
                                    
                                }
                                .title {
                                    position: absolute;
                                    bottom: 0;
                                    left: 2.5rem;
                                    @include font(small-5x, bold);
                                    color: custom-color(neutral, 080);
                                    background: custom-color(etc, 070);
                                    line-height: 120%;

                                    &::after {
										content: "";
										display: inline-block;
										position: absolute;
										top: 50%;
										right: -1.5rem;
										width: 0.7rem;
										height: 1.1rem;
										background: icon('arrow-right3', custom-color(neutral, 080)) no-repeat center;
										background-size: 100%;
										transform: translateY(-50%);
                                    }
                                }

                                &:hover {
                                    .icon {
                                        transform: scale(1.1);
                                        transition: all 0.8s ease-out;
                                    }
                                    .title {
                                        color: custom-color(primary, 090);
                                        &::after {
                                            background: icon('arrow-right3', custom-color(primary, 090)) no-repeat center;
                                        }
                                    }
                                }

                                &.two {
                                    top: 12rem;
                                    left: calc(50% + 34rem);
                                }
                                &.three {
                                    top: 34rem;
                                    left: calc(50% + 39rem);
                                    .title {
                                        bottom: -1.4rem;
                                        left: 3rem;
                                    }
                                }
                                &.four {
                                    top: 59rem;
                                    left: calc(50% + 23rem);
                                    .title {
                                        bottom: -1.4rem;
                                        left: 3.5rem;
                                    }
                                    .icon {
                                        top: 4.5rem;
                                        left: 5rem;
                                    }
                                }
                                &.five {
                                    top: 58.5rem;
                                    left: calc(50% - 23rem);
                                    .title {
                                        bottom: 0;
                                        left: 22rem;
                                    }
                                    .icon {
                                        top: 4.5rem;
                                        right: 5rem !important;
                                        left: auto;
                                    }
                                }
                                &.six {
                                    top: 34rem;
                                    left: calc(50% - 39rem);
                                }
                                &.seven {
                                    top: 12rem;
                                    left: calc(50% - 31rem);
                                }
                            }
                        }

                        &::after {
                            content: '';
                            display: block;
                            width: 58rem;
                            height: 58rem;
                            border-radius: 100%;
                            border: 1px dashed #393939;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            z-index: -1;
                            opacity: 0.6;
                        }
                    }
                    @include mobile {
                        // @include scroll-x;
                        //overflow-y: auto;
                        padding-left: 3rem;
                        transform: scale(1) !important;
                    }


                    @media only screen and (max-width: 1240px) {
                        transform: scale(0.95);
                        transition: all 0.3s ease-out;
                    }
                    @media only screen and (max-width: 1150px) {
                        transform: scale(0.9);
                    }

                }
            }

            + .section {
                padding-top: 18rem;
                @include mobile {
                    padding-top: 12rem;
                }
            }
        }
    }


    .solution-netzero {
        .section {
            .section-wrap {
                .section-body {
                    .post-list {
                    //    margin-top: 7.5rem;
                    }
                }
                .section-util {
                    .banner-list {
                        .banner-item {
                            .banner-wrap {

                                .banner-body {
                                    height: 100%;
                                    position: relative;
                                    margin-top: 6rem;

                                    &::after {
                                        content: '';
                                        display: block;
                                        position: absolute;
                                        top: 0;
                                        left: calc(50% - 50vw);
                                        width: 100vw;
                                        height: 32rem;
                                        background: url('../../assets/images/solution/net-zero/net-zero-bg.jpg') no-repeat center;
                                        background-size: cover;
                                        z-index: -1;

                                        @include mobile {
                                            background: url('../../assets/images/solution/net-zero/net-zero-bg-mo.jpg') no-repeat center;
                                            background-size: cover;
                                            height: 100%;
                                        }
                                    }
                                    .para {
                                        max-width: 144rem;
                                        height: 100%;
                                        padding: 11.2rem 0;
                                        display: flex;
                                        align-items: center;
                                        @include font(small, medium);
                                        color: custom-color(neutral, 000);

                                        @include mobile {
                                            padding: 5rem 0 17.7rem;
                                            @include font(small-3x, medium);
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                .section-figure {
                    img {
                        @include mobile {
                            width: 100%;
                            padding-bottom: 6rem;
                        }
                    }
                }

            }

            &.big-image {
                .section-wrap {
                    .section-figure {
                        margin-bottom: 10rem;
                        @include mobile {
                            margin-bottom: 0;
                        }
                    }
                }
            }

            &.process-bg {
                position: relative;
                padding-bottom: 18rem;

                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    z-index: -1;
                    top: 0;
                    bottom: 0;
                    left: calc(50% - 50vw);
                    width: 100vw;
                    height: 100%;
                    background: custom-color(etc, 070);
                }
            }



            &.story1 {
                
                 .section-wrap {
                    height: 100vh;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    @include mobile {
                        height: auto;
                    }

                    .section-body {
                        width: 100%;
                    }
                }
            }
        }
    }

}